<template>
    <div class="container">
        <h2>Validator Form校验</h2>

        <h3>Form基本校验和清除校验</h3>
        <PExample :code="code">
            <el-form
                ref="form"
                :model="form"
                :rules="rules"
                label-width="150px"
                label-suffix=":"
                style="width: 600px">
                <el-form-item label="法人姓名" prop="name">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
                <el-form-item label="法人身份证号" prop="id">
                    <el-input v-model="form.id"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onRemoveValid">清除校验（clearValidate）</el-button>
                    <el-button type="primary" @click="onRemoveField">清除校验（resetField）</el-button>
                </el-form-item>
            </el-form>
        </PExample>

        <h3>Form数字校验（带校验结果icon）</h3>
        <PExample :code="code">
            <el-form
                :model="form2"
                :rules="rules2"
                label-width="150px"
                label-suffix=":"
                status-icon
                style="width: 500px">
                <el-form-item label="商品名称" prop="name">
                    <el-input v-model="form2.name"></el-input>
                </el-form-item>
                <el-form-item label="金额" prop="price">
                    <el-input v-model="form2.price"></el-input>
                </el-form-item>
            </el-form>
        </PExample>

        <h3>Form动态校验</h3>
        <PExample :code="code3">
            <el-form :model="form3" label-width="150px">
                <el-form-item
                    v-for="(item, index) in form3.domains"
                    :key="index"
                    :label="`域名${index + 1}`"
                    :prop="`domains[${index}].value`"
                    :rules="domainRules">
                    <el-input v-model="item.value" style="width: 300px; margin-right: 10px"></el-input>
                    <i class="el-icon-delete" @click="onRemoveDomain(index)"></i>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onAddDomain">新增域名</el-button>
                </el-form-item>
            </el-form>
        </PExample>

        <h3>Form二选一</h3>
        <PExample :code="code4">
            <el-form :model="form4" :rules="rules4" label-width="150px" style="width: 500px">
                <el-form-item
                    label="姓名"
                    prop="name">
                    <el-input v-model="form4.name"></el-input>
                </el-form-item>
                <el-form-item
                    label="联系方式"
                    prop="contactType">
                    <el-input v-model="form4.telephone" style="width: 150px"></el-input> 或 <el-input v-model="form4.mobile" style="width: 150px"></el-input>
                </el-form-item>
            </el-form>
        </PExample>

        <h3>Form表格校验</h3>
        <PExample :code="code">
            <TableValid />
        </PExample>
    </div>
</template>

<script>
import TableValid from './components/TableValid.vue'
export default {
    name: 'validator',
    components: {
        TableValid
    },
    data () {
        return {
            form: {},
            form2: {},
            form3: {
                domains: [
                    { value: '' }
                ]
            },
            form4: {
                telephone: '',
                mobile: ''
            },
            rules: {
                name: [
                    { required: true, whitespace: true, message: '此项为必填项' }
                ],
                id: [
                    { required: true, whitespace: true, message: '此项为必填项' }
                ]
            },
            rules2: {
                name: [
                    { required: true, whitespace: true, message: '此项为必填项' }
                ],
                price: [
                    { required: true, whitespace: true, message: '此项为必填项' },
                    {
                        type: 'number',
                        transform (value) {
                            return value - 0
                        },
                        message: '只能输入数字'
                    }
                ]
            },
            domainRules: [
                { required: true, whitespace: true, message: '此项为必填项' }
            ],
            code: ''
        }
    },
    computed: {
        rules4 () {
            return {
                name: [
                    { required: true, message: '此项为必填项' }
                ],
                contactType: [
                    {
                        required: true,
                        validator: (rule, value, callback) => {
                            if (!(this.form4.telephone || this.form4.mobile)) {
                                return callback(new Error('此项为必填项'))
                            }
                            return callback()
                        }
                    }
                ]
            }
        },
        code3 () {
            return `
<template>
    <el-form :model="form3" label-width="150px">
        <el-form-item
            v-for="(item, index) in form3.domains"
            :key="index"
            :label="\`域名\${index + 1}\`"
            :prop="\`domains[\${index}].value\`"
            :rules="domainRules">
            <el-input v-model="item.value" style="width: 300px; margin-right: 10px"></el-input>
            <i class="el-icon-delete" @click="onRemoveDomain(index)"></i>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="onAddDomain">新增域名</el-button>
        </el-form-item>
    </el-form>
</template>

<script>
    export default {
        data () {
            form3: {
                domains: [
                    { value: '' }
                ]
            },
            domainRules: [
                { required: true, whitespace: true, message: '此项为必填项' }
            ]
        },
        methods: {
            onAddDomain () {
                this.form3.domains.push({
                    value: ''
                })
            },
            onRemoveDomain (index) {
                this.form3.domains.splice(index, 1)
            }
        }
    }
<\/script>
            `
        },
        code4 () {
            return `
<template>
    <el-form :model="form" :rules="rules" label-width="150px" style="width: 500px">
        <el-form-item
            label="姓名"
            prop="name">
            <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item
            label="联系方式"
            prop="contactType">
            <el-input v-model="form.telephone" style="width: 150px"></el-input> 或 <el-input v-model="form.mobile" style="width: 150px"></el-input>
        </el-form-item>
    </el-form>
</template>

<script>
    export default {
        data () {
            form: {
                telephone: '',
                mobile: ''
            }
        },
        computed: {
            rules () {
                return {
                    name: [
                        { required: true, message: '此项为必填项' }
                    ],
                    contactType: [
                        {
                            required: true,
                            validator: (rule, value, callback) => {
                                if (!(this.form4.telephone || this.form4.mobile)) {
                                    return callback(new Error('此项为必填项'))
                                }
                                return callback()
                            }
                        }
                    ]
                }
            }
        }
    }
<\/script>
            `
        }
    },
    methods: {
        onAddDomain () {
            this.form3.domains.push({
                value: ''
            })
        },
        onRemoveDomain (index) {
            this.form3.domains.splice(index, 1)
        },
        onRemoveValid () {
            // clearValidate只是清空校验信息
            this.$refs.form.clearValidate()
        },
        onRemoveField () {
            // resetFields清空校验信息和字段信息
            this.$refs.form.resetFields()
        }
    }
}
</script>

<style lang="scss" scoped>

</style>
